<template>
  <div v-show="menuVisible">
    <div ref="menuWrap" class="menu">
      <div class="menu-header"></div>
      <div class="menu-check-wrap">
        <Checkbox
          v-model="checkAll"
          :style="{
            display:'block',
            margin:'0',
            padding:'5px',

          }"
          :indeterminate="isIndeterminate"
          @change="handleCheckAllChange"
        ></Checkbox>
        <CheckboxGroup v-model="checkedHeaders" @change="handleCheckedHeadersChange">
          <Checkbox
            v-for="(header ) in headers"
            :key="header.prop"
            :label="header.prop"
            :style="{
              display:'block',
              margin:'0',
              padding:'5px',

            }"
          >{{ header.label }}</Checkbox>
        </CheckboxGroup>
      </div>
    </div>
  </div>
</template>

<script>

import { Checkbox, CheckboxGroup } from "element-ui";

import $ from "jquery";
export default {
  components: {
    Checkbox,
    CheckboxGroup
  },
  props: {
    getTableObj: {
      type: Function,
      default: () => null
    }
  },
  data() {
    return {
      menuVisible: false,
      checkAll: false,
      isIndeterminate: true,
      checkedHeaders: []
    };
  },

  computed: {
    headers() {
      const tableObj = this.getTableObj();
      if (tableObj) {
        return this.flatHeaders(tableObj.tableHeader);
      }
      return [];
    }
  },
  mounted() {},
  methods: {
    /**
     * 
     * @param  {Array} tableHeader 
     */
    flatHeaders(tableHeader) {
      let res = [];
      tableHeader &&
        tableHeader.map(item => {
          res.push(item);
          if (item.children) {
            res = res.concat(this.flatHeaders(item.children));
          }
        });
      return res;
    },
    /**
     * 
     */
    setCheckedHeaders() {
      const tableObj = this.getTableObj();
      if (tableObj) {
        return tableObj.showHeadersState;
      }
      return [];
    },
    /**
     * 
     *  @param  {Event} MouseEvent 
     */

    rightClick(MouseEvent) {
      this.checkedHeaders = this.setCheckedHeaders();
      this.handleCheckedHeadersChange(this.checkedHeaders);
      // 
      this.menuVisible = false;
      this.menuVisible = true;
      const menu = this.$refs.menuWrap;
      document.addEventListener("click", this.hidenMenu);
      menu.style.display = "block";
      menu.style.left = MouseEvent.clientX - 0 + "px";
      menu.style.top = MouseEvent.clientY - 0 + "px";
    },
    /**
     * 
     *  @param  {Event} e 
     */
    hidenMenu(e) {
      if (
        !$(e.target)
          .parents()
          .filter(this.$refs.menuWrap).length
      ) {
        this.menuVisible = false;
        document.removeEventListener("click", this.hidenMenu);
      }
    },
    /**
     * 
     *  @param  {Object} val 
     */
    handleCheckAllChange(val) {
      this.checkedHeaders = val ? this.headers.map(o => o.prop) : [];
      this.isIndeterminate = false;
      this.handleCheckedHeadersChange(this.checkedHeaders);
    },
    /**
     * table
     *  @param  {Array} value 
     */
    handleCheckedHeadersChange(value) {
      console.log(value);
      const tableObj = this.getTableObj();
      tableObj.showHeadersState = value;

      const checkedCount = value.length;
      this.checkAll = checkedCount === this.headers.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.headers.length;

      this.$nextTick(() => {
        if (tableObj.$refs.table && tableObj.$refs.table.doLayout) { tableObj.$refs.table.doLayout(); }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.menu {
  padding: 0;
  position: fixed;
  border: 1px solid #eee;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.05);
  background-color: #fff;
  margin: 0;
  z-index: 120;
  &-header {
    padding: 7px 10px;
    border-bottom: 1px solid #eee;
  }
  &-check-wrap {
    padding: 10px;
    max-height: 250px;
    overflow: auto;
  }
  &-list {
    font-size: 14px;
    padding: 0;
    margin: 0;
    list-style: none;

    &-item:hover {
      background-color: rgba(0, 0, 0, 0.05);
    }
    &-item {
      font-size: 15px;
      list-style: none;
      padding: 5px 10px;
      margin: 0;
    }
  }
}
</style>

