/**
 * Parse the time to string
 * @param {(Object|string|number)} time
 * @param {string} cFormat
 * @returns {string | null}
 */
export function parseTime(time, cFormat) {
  if (arguments.length === 0) {
    return null;
  }
  const format = cFormat || "{y}-{m}-{d} {h}:{i}:{s}";
  let date;
  if (typeof time === "object") {
    date = time;
  } else {
    if (typeof time === "string" && /^[0-9]+$/.test(time)) {
      time = parseInt(time);
    }
    if (typeof time === "number" && time.toString().length === 10) {
      time = time * 1000;
    }
    date = new Date(time);
  }
  const formatObj = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  };
  const time_str = format.replace(/{([ymdhisa])+}/g, (result, key) => {
    const value = formatObj[key];
    // Note: getDay() returns 0 on Sunday
    if (key === "a") {
      return ["", "", "", "", "", "", ""][value];
    }
    return value.toString().padStart(2, "0");
  });
  return time_str;
}

/**
 * @param {number} time
 * @param {string} option
 * @returns {string}
 */
export function formatTime(time, option) {
  if (("" + time).length === 10) {
    time = parseInt(time) * 1000;
  } else {
    time = +time;
  }
  const d = new Date(time);
  const now = Date.now();

  const diff = (now - d) / 1000;

  if (diff < 30) {
    return "";
  } else if (diff < 3600) {
    // less 1 hour
    return Math.ceil(diff / 60) + "";
  } else if (diff < 3600 * 24) {
    return Math.ceil(diff / 3600) + "";
  } else if (diff < 3600 * 24 * 2) {
    return "1";
  }
  if (option) {
    return parseTime(time, option);
  } else {
    return (
      d.getMonth() +
      1 +
      "" +
      d.getDate() +
      "" +
      d.getHours() +
      "" +
      d.getMinutes() +
      ""
    );
  }
}

/**
 * @param {string} url
 * @returns {Object}
 */
export function param2Obj(url) {
  const search = url.split("?")[1];
  if (!search) {
    return {};
  }
  return JSON.parse(
    '{"' +
      decodeURIComponent(search)
        .replace(/"/g, '\\"')
        .replace(/&/g, '","')
        .replace(/=/g, '":"')
        .replace(/\+/g, " ") +
      '"}'
  );
}

/**
 * @param {Function} func
 * @param {number} wait
 * @param {boolean} immediate
 * @return {*}
 */
export function debounce(func, wait, immediate) {
  let timeout, args, context, timestamp, result;

  const later = function() {
    // 
    const last = +new Date() - timestamp;

    //  last  wait
    if (last < wait && last > 0) {
      timeout = setTimeout(later, wait - last);
    } else {
      timeout = null;
      // immediate===true
      if (!immediate) {
        result = func.apply(context, args);
        if (!timeout) context = args = null;
      }
    }
  };

  return function(...args) {
    context = this;
    timestamp = +new Date();
    const callNow = immediate && !timeout;
    // 
    if (!timeout) timeout = setTimeout(later, wait);
    if (callNow) {
      result = func.apply(context, args);
      context = args = null;
    }

    return result;
  };
}

function filterKeyValue(keyMap, key, o) {
  if (typeof keyMap[key] == "function") {
    return keyMap[key](o);
  } else if (typeof keyMap[key] == "string") {
    return o[keyMap[key]];
  }
}

export function setTreeData(
  data,
  parentItem,
  keyMap = {
    label: "divisionName",
    value: o => o.value || o.id,
    children: "childrenAddress",
    setObjKeyArr: false
  }
) {
  return data.map(item => {
    item.label = filterKeyValue(keyMap, "label", item);
    item.value = filterKeyValue(keyMap, "value", item);
    const getChilds = filterKeyValue(keyMap, "children", item);
    const hasChildren = !!(getChilds && getChilds.length);

    item.parentName = parentItem ? parentItem.label : "";

    if (hasChildren) {
      item.children = getChilds || [];
      if (typeof keyMap.children == "string") delete item[keyMap.children];
      item.children = setTreeData(item.children, item, keyMap);
    }
    if (!keyMap.setObjKeyArr) {
      return item;
    } else {
      const setItem = {};
      [...keyMap.setObjKeyArr, "children", "parentName"].map(k => {
        setItem[k] = item[k];
      });
      return setItem;
    }
  });
}

/**
 *   
 * @param {Array} data   
 * @param {Object} parentItem  
 * @return {Array}
 */

export const setCityTreeData = setTreeData;

/**
 *   
 * @param {Array} data   
 * @param {Array} lvKeys==label  

 */

export function setTreeLvKeys(data, lvKeys = []) {
  return data.map((item, i) => {
    item.lvKeys = [...lvKeys, i];
    if (item.children && item.children.length) {
      item.children = setTreeLvKeys(item.children, [...lvKeys, i]);
    }
    return item;
  });
}

/**
 *   
 * @param {Array} data   
 * @param {Array} lvKeys==label  

 */

export function getTreeDataByLvKeys(data, lvKeys = []) {
  let findData = null;
  lvKeys.map((key, idx) => {
    if (idx == 0) {
      if (data[key]) {
        findData = data[key];
      }
    } else {
      if (findData && findData.children && findData.children[key]) {
        findData = findData.children[key];
      } else {
        findData = null;
      }
    }
  });
  return findData;
}

/**
 *   
 * @param {Array} data   
 * @param {string} val  
 * @param {string} labelKey==label  key
 * @param {string} key==value  key
 * @return {String}
 */
export function getLabel(data, val, labelKey = "label", key = "value") {
  let res = val;
  data.map(o => {
    if (o[key] == val) {
      res = o[labelKey];
    }
  });
  return res;
}

/**
 *   
 * @param {Object} setData  
 * @param {string} key   key
 * @param {Function} setFilterFunc  
 * @return {Object}
 */
export function startEndDataFilter(setData, key, startKey = '${key}Start', endKey = '${key}End') {
  if (setData[key] && setData[key][0] && setData[key][1]) {
    setData[startKey.replace('${key}', key)] = setData[key][0];
    setData[endKey.replace('${key}', key)] = setData[key][1];
    delete setData[key];
  } else {
    delete setData[key];
  }
  return setData;
}

/**
 *   

 * @param {string} val  

 * @return {Number}
 */
export function getByteLen(val) {
  var len = 0;
  for (var i = 0; i < val.length; i++) {
    var a = val.charAt(i);
    if (a.match(/[^\x00-\xff]/gi) != null) {
      len += 2;
    } else {
      len += 1;
    }
  }
  return len;
}
