<script>
import { Button, Dialog } from "element-ui";
import cx from "classnames";
import QuickForm from "../../QuickForm";
import submitToServerMix from "../../commonMix/submitToServerMix";
import "./style.scss";
import request from "../../utils/request";

export default {
  name: "PowerfulBtn",
  components: {
    Button,
    QuickForm
  },
  mixins: [submitToServerMix],
  model: {
    prop: "value", // props
    event: "changePowerfulBtnValue" // emit
  },
  props: {
    asDom: {

      type: [String, Object],
      default: ""
    },
    noWrap: {
      type: [Boolean, String],
      default: false
    },
    block: {
      type: Boolean,
      default: false
    },

    onClick: {
      type: Function,
      default: () => {}
    },
    type: {
      type: String,
      default: "diy"
    },
    size: {
      type: String,
      default: ""
    },
    // props  start
    // Formprops
    toSetQuickformAttr: {
      type: Object,
      default: () => ({})
    },
    buttonProps: {
      type: Object,
      default: () => ({})
    },
    dialogDefaultAttrs: {
      type: Object,
      default: () => ({ })
    },
    dialogAttrs: {
      type: Object,
      default: () => ({})
    },
    defaultCreatedDialogForm: {
      type: Boolean,
      default: false
    },
    resetToSetValue: {
      type: Boolean,
      default: false
    },
    // props  end

    //  start
    value: {
      type: Object,
      default: () => ({})
    },
    formRenderDataFilter: {
      type: Function,
      default: res => res
    },
    //  end

    //   start
    /*  */
    beforeGetFormDataDialogShow: {
      type: Function,
      default: cb => cb()
    },
    //   end
    // confirmToFetch     start
    confirmText: {
      type: String,
      default: ""
    },
    confirmTitle: {
      type: String,
      default: ""
    },
    confirmType: {
      type: String,
      default: "warning"
    },

    // confirmToFetch     end
    // form start
    getFormDataUrl: {
      type: String,
      default: ""
    },
    getFormDataFetchType: {
      type: String,
      default: "get"
    },
    getFormDataFetchData: {
      type: Object,
      default: () => ({})
    },
    getFormDataFetchDataFilter: {
      type: Function,
      default: res => res
    },
    getFormDataResDataFilter: {
      type: Function,
      default: res => res
    },
    formFetchToSetValueFilter: {
      type: Function,
      default: res => res
    },
    loadingText: {
      type: String,
      default: ""
    }

    // form end
  },
  data() {
    return {
      formValue: this.value,
      dialogVisible: false
    };
  },
  watch: {
    value: function(newVal) {
      if (this.resetToSetValue) this.formValue = newVal;
    },
    formValue: function(newVal) {
      this.$emit("changePowerfulBtnValue", newVal);
    },
    dialogVisible() {
      console.log(11111111);
    }
  },
  methods: {
    // 
    onBtnClick(e) {
      e.stopPropagation();

      const { type, onClick, formValue } = this;
      if (typeof onClick == "function") {
        const res = onClick(formValue);
        if (typeof res == "boolean" && !res) {
          return;
        }
      }
      switch (type) {
        // case "link":
        //   browserHistory.push(url);
        //   break;
        case "fetch":
          this.onFetchBtnClick();
          break;
        case "form":
          this.onFormBtnClick();
          break;
        case "confirmToFetch":
          this.onConfirmToFetchBtnClick();
          break;
        // case "back":
        //   if (window.reactHistory) {
        //     window.reactHistory.goBack();
        //   } else {
        //     browserHistory.goBack();
        //   }
        //   break;
        default:
          //  onClick();
          break;
      }
    },
    onFetchBtnClick() {
      this.goFetchFunc(this.fetchData);
    },
    onConfirmToFetchBtnClick() {
      const { confirmText, confirmTitle, confirmType, fetchData } = this;

      this.$confirm(confirmText, confirmTitle, {
        confirmButtonText: "",
        cancelButtonText: "",
        type: confirmType
      })
        .then(() => {
          this.goFetchFunc(fetchData);
        })
        .catch(() => {});
    },
    onFormBtnClick() {
      const { beforeGetFormDataDialogShow } = this;

      this.getFormValueData(setValue => {
        //  Dialog,
        beforeGetFormDataDialogShow(v => {
          this.formValue = v || setValue;
          this.dialogVisible = true;
        }, setValue);
      }, this.fetchData);
    },
    async getFormValueData(cb, mergeData = {}) {
      // 
      const {
        getFormDataUrl,
        getFormDataFetchType,
        getFormDataFetchData,
        getFormDataFetchDataFilter,
        isResultSuccess,
        formFetchToSetValueFilter,
        getFormDataResDataFilter,
        defaultResFilter
      } = this;
      let setVal = this.getJson(this.formValue);
      let getFormDataFetchDataParams = { ...mergeData };
      if (getFormDataUrl) {
        this.changeLoadingStatus(true);
        getFormDataFetchDataParams = getFormDataFetchDataFilter(
          Object.assign({}, getFormDataFetchData, mergeData)
        );
        // 
        let res = await request({
          url: getFormDataUrl,
          method: getFormDataFetchType,
          [getFormDataFetchType == "get" ? "params" : "data"]: getFormDataFetchDataParams

        });

        try {
          res = defaultResFilter(res);
          res = getFormDataResDataFilter(res);
          this.changeLoadingStatus(false);
          if (isResultSuccess(res)) {
            setVal = {
              ...setVal,
              ...res.data
            };
            cb(formFetchToSetValueFilter(setVal, getFormDataFetchDataParams));
            // 
          } else {
            throw res;
          }
        } catch (res) {
          this.changeLoadingStatus(false);

          this.$notify({
            title: res.message || "",
            type: "error"
          });
        }
      } else {
        cb(formFetchToSetValueFilter(setVal, getFormDataFetchDataParams));
      }
    },
    onFormSubmit(v) {
      // 

      const formData = this.getJson(v);
      const setFetchData = {
        ...this.fetchData,
        ...formData
      };
      if (!this.url) {
        // 
        this.toSetQuickformAttr.onSubmit &&
          this.toSetQuickformAttr.onSubmit(v, setFetchData, this);
      } else {
        this.goFetchFunc(setFetchData);
      }
    },

    onSuccessReset() {
      // 
      const { type } = this;
      if (type == "form") {
        this.dialogVisible = false;
        this.formValue = {};
      }
    },
    /* async goFetchFunc   */

    dialogRender() {
      // 
      const {
        dialogVisible,
        type,
        dialogDefaultAttrs,
        dialogAttrs,
        formValue,
        toSetQuickformAttr,
        fetchData,
        formRenderDataFilter,
        defaultCreatedDialogForm
      } = this;
      const { renderData, ...filterQuickformAttr } = toSetQuickformAttr;
      const setForm = this.$createElement("quick-form", {
        ref: "QuickForm",
        attrs: {
          ...filterQuickformAttr,
          renderData: formRenderDataFilter(renderData, formValue, fetchData),
          onSubmit: this.onFormSubmit,
          value: formValue
        },
        model: {
          value: this.formValue,
          callback: $$v => {
            this.formValue = $$v;
          }
        }
      }); // quick form
      return type == "form" && (defaultCreatedDialogForm || dialogVisible)
        ? this.$createElement(
          Dialog,
          {
            attrs: {
              title: "",
              width: "900px",
              modal: false,
              ...dialogDefaultAttrs,
              ...dialogAttrs,
              visible: dialogVisible
            },
            on: {
              "update:visible": $event => {
                this.dialogVisible = $event;
              }
            }
          },
          [setForm]
        )
        : null;
    }
  },
  render(h) {
    const {
      asDom,
      loading,
      loadingText,
      block,
      size,
      style,
      buttonProps
    } = this;

    const setButtonAttrs = {
      block,
      size: size || undefined,
      ...buttonProps,
      loading
    };

    const setWrapClass = cx({
      "powerful-btn-wrap": true,
      block: block
    });
    const buttonVnode = h(
      asDom || Button,
      {
        attrs: setButtonAttrs,
        style,
        on: {
          click: !loading ? this.onBtnClick : undefined
        }
      },
      loading ? loadingText : this.$slots.default
    );
    if (this.noWrap) {
      /**/

      const bodyLineHeight =
        parseFloat(window.getComputedStyle(document.body, null).lineHeight) /
        parseFloat(window.getComputedStyle(document.body, null).fontSize);
      return this.dialogVisible ? (
        <div
          style={{
            textAlign: "left",
            zIndex: 2000,
            lineHeight: bodyLineHeight
          }}
          class={`el-button el-button--${
            setButtonAttrs.type ? setButtonAttrs.type : "default"
          } el-button--${
            setButtonAttrs.size ? setButtonAttrs.size : "mini"
          } ${setButtonAttrs.class || ""}`}
        >
          {buttonProps.icon ? <i class={buttonProps.icon} /> : null}
          {this.dialogRender()}
          {loading ? loadingText : this.$slots.default}
        </div>
      ) : (
        buttonVnode
      );
    }
    return (
      <div class={setWrapClass}>
        {this.dialogRender()}
        {buttonVnode}
      </div>
    );
  }
};
</script>
