import Qs from "qs";
import request from "../utils/request";

import isEqual from "lodash/isEqual";
import isArray from "lodash/isArray";
import cloneDeep from "lodash/cloneDeep";
import isFunction from "lodash/isFunction";

export default {
  props: {
    /**
     * url
     */
    url: {
      type: String
    },
    /**
     * 
     */
    fetchType: {
      type: String,
      default: "get"
    },
    /**
     * 
     */
    fetchData: {
      type: [Object, Array, String],
      default: () => ({})
    },
    /**
     * 
     */
    beforeFetchDataFilter: {
      type: Function,

      default: res => {
        const { currentPage, pageSize, ...otherData } = res;
        return {
          ...otherData,
          pageNum: currentPage,
          pageSize: pageSize
        };
      }
    },
    /**
     * 
     */
    disabledFetchFilter: {
      type: Function,

      default: res => {
        return false;
      }
    },
    /**
     * 
     */
    fetchDataFilter: {
      type: Function,
      default: res => res
    },
    /**
     * jsonform
     */
    fetchDataType: {
      type: String,
      default: "json"
    },
    /**
     * 
     */
    onLoadDataSuccess: {
      type: Function,
      default: res => res
    },
    /**
     * 
     */
    isSuccess: {
      type: Function,
      default: res => res
    },
    /**
     * 
     */
    resDataFilter: {
      type: Function,
      default: res => res
    },
    /**
     * 
     */
    data: {
      type: Array,
      default: () => []
    },
    /**
     * 
     */
    pagination: {
      type: Boolean,
      default: true
    },
    /**
     *  
     */
    pageProps: {
      type: Object,
      default: () => ({})
    }
  },

  data(e) {
    return {
      loading: false,
      lastFetchData: {}, // 
      listData: this.data,
      total: e.data.length,
      currentPage: 1,
      stateFetchData: {},
      pageSize: this.pageProps.pageSize || 10
    };
  },
  created() {
    this.loadData();
  },
  watch: {
    fetchData: function(newVal, oldVal) {
      if (!isEqual(newVal, oldVal)) {
        this.lazyLoad(true);
      }
    },
    data: function(newData, oldData) {
      if (isArray(newData) && !isEqual(newData, oldData)) {
        this.listData = newData;
      }
    }
  },
  methods: {
    /**
     * 
     * @param  {Boolean} reload 
     */
    lazyLoad(reload) {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.loadData(reload);
      }, 500);
    },
    /**
     * 
     * @param  {Object} d 
     */
    getJson(d) {
      return cloneDeep(d);
    },
    /**
     * 
     * @param  {Array} data 
     * @param  {Number} total 
     */
    resetData(data = [], total = 0) {
      this.loading = false;
      this.listData = data;
      this.total = total;
    },
    /**
     * 
     * @param  {Boolean} resetFirstPage 
     */
    loadData(resetFirstPage = false) {
      console.log("loading.....");
      if (!this.url) return;
      if (resetFirstPage) {
        this.currentPage = 1;
      }
      const { currentPage, pageSize, stateFetchData } = this;

      let fetchDataAll = {
        ...this.getJson(this.fetchData),
        currentPage,

        pageSize,
        ...this.getJson(stateFetchData)
      };
      delete fetchDataAll.isTrusted;
      fetchDataAll = this.beforeFetchDataFilter(fetchDataAll);
      const setUrl = isFunction(this.url)
        ? this.url(fetchDataAll)
        : this.url;
      fetchDataAll = this.fetchDataFilter(fetchDataAll);

      if (this.fetchType == "get") {
        fetchDataAll = {
          params: fetchDataAll
        };
      }
      const disabledFetch = this.disabledFetchFilter(fetchDataAll);
      if (disabledFetch) {
        return;
      }
      let setConf = {};
      if (this.fetchDataType == "form") {
        setConf = {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
          },
          transformRequest: [
            function(data) {
              data = Qs.stringify(data);
              return data;
            }
          ]
        };
      }
      this.loading = true;
      this.lastFetchData = fetchDataAll;
      request({
        url: setUrl,
        method: this.fetchType,
        [this.fetchType == "get" ? "params" : "data"]: fetchDataAll,
        ...setConf
      }).then(res => {
        res = this.resDataFilter(res);
        this.loading = false;
        if (this.isSuccess(res)) {
          this.resetData(res.list, res.total);
          if (this.onLoadDataSuccess) this.onLoadDataSuccess(res);
        }
        if (this.afterSuccessFunc) {
          this.afterSuccessFunc(res);
        }
      })
        .catch(err => {
          if (this.afterErrorFunc) {
            this.afterErrorFunc(err);
          }
          this.resetData();
        });
    },
    /**
     * 
     * @param  {Number} currentPage 
     */
    handleCurrentChange(currentPage) {
      this.currentPage = currentPage;
      this.loadData();
    },
    /**
     * 
     * @param  {Number} size 
     */
    handleSizeChange(size) {
      this.currentPage = 1;
      this.pageSize = size;
      this.loadData();
    }
  }
};
