import Qs from "qs";
import request from "../utils/request";
import isEqual from "lodash/isEqual";
import cloneDeep from "lodash/cloneDeep";
import merge from "lodash/merge";
import isFunction from "lodash/isFunction";
/*
  findInfoDataMix * 
*/

export default {
  props: {
    /**
     * 
     */
    disabledFetch: {
      type: Boolean,
      default: false
    },
    /**
     * url
     */
    url: {
      type: String
    },
    /**
     * 
     */
    fetchType: {
      type: String,
      default: "get"
    },
    /**
     * 
     */
    fetchData: {
      type: [Object, Array, String],
      default: () => ({})
    },
    /**
     * 
     */
    beforeFetchDataFilter: {
      type: Function,

      default: res => {
        return res;
      }
    },
    /**
     * 
     */
    fetchDataFilter: {
      type: Function,
      default: res => res
    },
    /**
     * jsonform
     */
    fetchDataType: {
      type: String,
      default: "json"
    },
    /**
     * 
     */
    onLoadDataSuccess: {
      type: Function,
      default: res => res
    },
    /**
     * 
     */
    isSuccess: {
      type: Function,
      default: res => res
    },
    /**
     * 
     */
    resDataFilter: {
      type: Function,
      default: res => res
    },
    /**
     * 
     */
    propData: {
      type: [Object, Array, String],
      default: () => ({})
    },
    /**
     * 
     */
    configList: {
      type: [Boolean, Array],
      default: false
    },
    /**
     *  
     */
    fetchListType: {
      type: String,
      default: "all" // all  every
    }
  },

  data(e) {
    return {
      loading: false,
      infoData: this.propData,
      stateFetchData: {}
    };
  },
  created() {
    if (!this.disabledFetch) this.loadData();
  },
  watch: {
    fetchData: function(newVal, oldVal) {
      if (!isEqual(newVal, oldVal) && !this.disabledFetch) {
        clearTimeout(this.timer);
        this.timer = setTimeout(() => {
          this.loadData(true);
        }, 500);
      }
    }

  },
  methods: {
    /**
     * 
     * @param  {Object} d 
     */
    getJson(d) {
      return cloneDeep(d);
    },
    /**
     * 
     * @param  {Array} data  

     */
    resetData(data = {}) {
      this.loading = false;
      if (
        !this.url &&
        this.configList &&
        this.configList.length &&
        this.fetchListType == "every"
      ) {
        // 
        this.infoData = { ...merge(this.infoData, data) };
      } else {
        this.infoData = data;
      }
    },
    /**
     * 
     * @param  {Object} conf 
     * @param  {String} key name
     */
    getConf(conf, key) {
      return conf && conf[key] ? conf[key] : this[key];
    },
    /**
     * 
     * @param  {Object} conf 
     * @param  {Number}} index  idx
     */
    makeConf(conf, index) {
      const stateFetchData = this.getConf(conf, "stateFetchData"); // 
      const fetchData = this.getConf(conf, "fetchData"); // 
      const beforeFetchDataFilter = this.getConf(conf, "beforeFetchDataFilter"); // 
      const fetchDataFilter = this.getConf(conf, "fetchDataFilter"); // 
      const fetchType = this.getConf(conf, "fetchType"); // 
      const fetchDataType = this.getConf(conf, "fetchDataType"); // formjson
      let fetchDataAll = {
        ...this.getJson(fetchData),
        ...this.getJson(stateFetchData)
      };
      delete fetchDataAll.isTrusted;
      fetchDataAll = beforeFetchDataFilter(fetchDataAll);
      const setUrl = isFunction(conf.url)
        ? conf.url(fetchDataAll)
        : conf.url;
      fetchDataAll = fetchDataFilter(fetchDataAll);

      if (fetchType == "get") {
        fetchDataAll = {
          params: fetchDataAll
        };
      }
      let setConf = {};
      if (fetchDataType == "form") {
        setConf = {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
          },
          transformRequest: [
            function(data) {
              data = Qs.stringify(data);
              return data;
            }
          ]
        };
      }

      return request({
        url: setUrl,
        method: fetchType,
        [fetchType == "get" ? "params" : "data"]: fetchDataAll,
        ...setConf
      }).then(res =>
        typeof index == "number"
          ? { ...res, confName: conf.name, confIdx: index }
          : res
      );
    },
    /**
     * 
     */
    loadData() {
      console.log("loading.....");

      if (this.url) {
        // 
        this.loading = true;
        this.makeConf(this)
          .then(this.onFetchSuccess)
          .catch(this.onFetchError);
      } else if (this.configList && this.configList.length) {
        // 
        this.loading = true;
        const PromiseList = [];

        this.configList.map((conf, i) => {
          PromiseList.push(this.makeConf(conf, i));
        });
        if (this.fetchListType == "all") {
          // 
          Promise[this.fetchListType](PromiseList)
            .then(this.mergeDataFilter)
            .then(this.onFetchSuccess)
            .catch(this.onFetchError);
        } else if (this.fetchListType == "every") {
          // 
          PromiseList.map(PromiseItem => {
            PromiseItem.then(this.everyFetchDataFilter)
              .then(this.onFetchSuccess)
              .catch(this.onFetchError);
          });
        }
      }
    },
    /**
     *  
     * @param  {Object} res 
     */
    mergeDataFilter(res) {
      //  
      const resData = {
        success: true,
        data: {}
      };
      res.map(o => {
        resData.data[o.confName || `data${o.confIdx}`] = o.data;
      });

      return resData;
    },
    /**
     *  
     * @param  {Object} res 
     */
    everyFetchDataFilter(res) {
      const resData = {
        success: true,
        data: {}
      };

      resData.data[res.confName || `data${res.confIdx}`] = res.data;

      return resData;
    },
    /**
     * 
     * @param  {Object} res 
     */
    onFetchSuccess(res) {
      res = this.resDataFilter(res);
      this.loading = false;

      if (this.isSuccess(res)) {
        this.resetData(res.data);
      }
      if (this.afterSuccessFunc) {
        this.afterSuccessFunc(res);
      }
    },
    /**
     * 
     * @param  {Object} err 
     */
    onFetchError(err) {
      this.resetData();
      throw err;
    }
  }
};
