import request from "../utils/request";
import Qs from "qs";
import cloneDeep from "lodash/cloneDeep";
import isFunction from "lodash/isFunction";
export default {
  props: {
    /**
     * url
     */
    url: {
      type: [String, Function],
      default: ""
    },
    /**
     * 
     */
    fetchType: {
      type: String,
      default: "post"
    },
    /**
     * 
     */
    fetchData: {
      type: Object,
      default: () => ({})
    },
    /**
     * 
     */
    successText: {
      type: String,
      default: ""
    },
    /**
     * 
     */
    beforeFetch: {
      type: Function
    },
    /**
     *  json  form
     */
    fetchDataType: {
      type: String,
      default: "json"
    },
    /**
     * 
     */
    afterSubmitSuccess: {
      type: Function,
      default: res => res
    },
    /**
     * 
     */
    afterSubmitError: {
      type: Function,
      default: res => res
    },
    /**
     * 
     */
    fetchDataFilter: {
      type: Function,
      default: res => res
    },
    /**
     * fetch
     */
    fetchConfig: {
      type: Object,
      default: () => ({})
    },
    /**
     * 
     */
    defaultResFilter: {
      type: Function,
      default: res => res
    },
    /**
     * 
     */
    isResultSuccess: {
      type: Function,
      default: res => res
    },
    /**
     * 
     */
    getFile: {
      type: Boolean,
      default: false
    },
    /**
     *  
     */
    showSuccessTip: {
      type: Boolean,
      default: true
    },
    /**
     *  
     */
    showErrorTip: {
      type: Boolean,
      default: true
    }
  },

  data(e) {
    return {
      loading: false
    };
  },

  methods: {
    /**
     * 
     * @param  {Object} obj 
     */
    getJson(obj) {
      return cloneDeep(obj);
    },
    /**
     * 
     * @param  {Object} fetchData 
     */
    getFileFunc() {},
    /**
     * 
     * @param  {Object} resData 
     */
    onSuccessReset() {},
    /**
     * 
     * @param  {Boolean} type 
     */
    changeLoadingStatus(type) {
      this.loading = type;
      this.$emit("submitLoadingStatusChange", type);
    },
    /**
     * 
     * @param  {Object} fetchData 
     *  @param  {Function} disabledFilter 
     */
    async goFetchFunc(fetchData = this.fetchData, disabledFilter) {
      const {
        url,
        fetchType,
        successText,

        beforeFetch,
        afterSubmitSuccess,
        fetchDataFilter,
        getFile,
        defaultResFilter,
        isResultSuccess,
        showSuccessTip
      } = this;
      let goFetch = true; // 
      const setUrl = isFunction(url) ? url(fetchData) : url;
      if (!setUrl) return;
      if (fetchData) delete fetchData.isTrusted;
      if (isFunction(fetchDataFilter) && !disabledFilter) {
        fetchData = fetchDataFilter(fetchData); // 
      }
      if (beforeFetch) {
        goFetch = beforeFetch(fetchData); // 
      }

      let setFetchConfig = { ...this.fetchConfig };
      if (this.fetchDataType == "form") {
        setFetchConfig = {
          ...setFetchConfig,
          headers: {
            "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
          },
          transformRequest: [
            function(data) {
              data = Qs.stringify(data);
              return data;
            }
          ]
        };
      }
      if (fetchData) delete fetchData.isTrusted;
      if (goFetch) {
        this.changeLoadingStatus(true);
        if (getFile) {
          // 
          this.getFileFunc(fetchData);
          return;
        }
        try {
          let res = await request({
            url: setUrl,
            method: fetchType,
            [fetchType == "get" ? "params" : "data"]: fetchData,
            ...setFetchConfig
          });
          res = defaultResFilter(res);
          this.changeLoadingStatus(false);
          if (isResultSuccess(res)) {
            if (typeof afterSubmitSuccess == "function") {
              afterSubmitSuccess(res, fetchData);
            }
            this.$emit("submitSuccess", res, fetchData, this);
            if (showSuccessTip) {
              this.$notify({
                title: res.message || res.msg || successText,
                type: "success"
              });
            }
            this.onSuccessReset();
          } else {
            throw res || {};
          }
        } catch (err) {
          console.log(err);
          this.changeLoadingStatus(false);
          const resText = err.message ? err.message : err.msg || "";
          if (typeof this.afterSubmitError == "function") {
            this.afterSubmitError(err, fetchData);
          }
          this.$emit("submitError", err, fetchData, this);
          if (this.showErrorTip) {
            this.$notify({
              title: resText,
              type: "error"
            });
          }
        }
      }
    }
  }
};
